import React, { useEffect, useState } from "react";
import { Line } from "react-chartjs-2";
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
} from "chart.js";
ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend
);
function App() {
  const [chartData, setChartData] = useState({
    labels: [],
    datasets: [
      {
        type: "line",
        label: "BTC-USD",
        backgroundColor: "rgba(0, 0, 0, 0)",
        borderColor: "rgb(255, 99, 132)",
        backgroundColor: "rgba(255, 99, 132, 0.5)",
        borderWidth: "2",
        data: [],
      },
    ],
  });
  const [options] = useState({
    responsive: true,
    maintainAspectRatio: false,
    tooltips: {
      enabled: true,
    },
    scales: {
      xAxes: [
        {
          ticks: {
            autoSkip: true,
            maxTicksLimit: 10,
          },
        },
      ],
    },
  });
  useEffect(() => {
    //create the subscribe;
    const subscribe = {
      type: "subscribe",
      channels: [
        {
          name: "ticker",
          product_ids: ["BTC-USD"],
        },
      ],
    };
    //start the web socket
    const socket = new WebSocket("wss://ws-feed.pro.coinbase.com");
    // now open the socket and send the handshake call
    socket.onopen = () => {
      socket.send(JSON.stringify(subscribe));
    };

    // now get the message from server
    socket.onmessage = (e) => {
      const value = JSON.parse(e.data);
      if (value.type === "ticker") {
        const oldBtcDataSet = chartData.datasets[0];
        const newBtcDataSet = { ...oldBtcDataSet };
        newBtcDataSet.data.push(value.price);
        let newLabels = chartData.labels;
        newLabels.push(new Date().toLocaleTimeString());
        const newChartData = {
          datasets: [newBtcDataSet],
          labels: newLabels,
        };
        setChartData(newChartData);
      }
    };

    socket.onclose = (event) => {
      console.log("Event closed", event);
    };

    return () => socket.close();
  }, []);
  return (
    <div className="container mx-auto h-96">
      <Line data={chartData} options={options} />
    </div>
  );
}
export default App;

